.Pokecard {
  box-shadow: 0px 10px 20px rgba(51, 59, 63, 0.21);
  border-radius: 10px;
  width: 250px;
  padding: 10px;
  margin: 1rem;
  background: #fff;
}

.Pokecard img {
  transition: all ease 500ms;
}

.Pokecard img:hover {
  transform: scale(1.25);
}

.Pokecard-data {
  font-size: 80%;
}

.Pokecard-image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 250px;
  background-color: #eceff1;
  border-radius: 5px;
  margin-bottom: 1rem;
}